:root {
  --length: 5px;
  --length2: calc(var(--length) * 2);
  --length3: calc(var(--length) * 3);
}
/*public style*/
  html {
    font-size: 62.5%;
  }
	body {
    min-width: 1366px;
    line-height: 1.5;
    font-size: 14px;
    font-family: "Microsoft Yahei";
    color: #333;
  }
	* {
	  margin:0;
	  padding:0;
	  font-style:normal;
	  outline: none;
	  box-sizing: border-box;
	}
	img{
	  width:100%;
	  vertical-align: bottom;
	}
	ul, li {
	  list-style: none;
	}
	a {
	  text-decoration: none;
	  color: #3c5dc9;
	}
	a:hover {
	  outline: 0;
	}
	input,select,textarea,button{
	  font-family: "Microsoft Yahei";
	  font-size: 14px;
	  line-height: 1.5;
	  -webkit-appearance: none;
	}
	textarea {
	  resize: none;
	}
	input,select,textarea{
	  width:100%;
	  border: none;
	  background: none;
	  box-shadow: none;
	  -webkit-user-select: text;
	}
	button:focus,input:focus {
	  outline: 0;
	}
  .body{
    min-height: calc(100vh - 272px)
  }
  .fixed + .body{
    min-height: calc(100vh - 201px)
  }
/*display*/
	.block{
	  display: block;
	}
	.table{
	  display: table;
	}
	.inline{
	  display: inline;
	}
	.flex{
    display: flex;
	}
	.block_inline{
    display:inline-block;
    vertical-align: middle;
	}
	.block_middle{
	  vertical-align: middle;
	}
	.block_center{
	  margin-left: auto;
	  margin-right: auto;
	}
	.flex_1{
	  flex:1;
	}
	.flex_wrap{
	  display: flex;
	  flex-wrap: wrap;
	}
	.flex_column{
    display: flex;
    box-orient: vertical;
    flex-direction: column;
	}
  .flex_reverse{
    display: flex;
    flex-direction: row-reverse;
  }
	.flex_center{
    display: flex;
    align-items: center;
    justify-content: center;
	}
	.row_justify{
    display: flex;
    justify-content: space-between;
	}
  .h_justify{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
	.v_center{
    display: flex;
    align-items: center;
	}
/*position*/
  .absolute{
      position: absolute;
  }
  .fixed{
      position: fixed;
  }
  .relative{
      position: relative;
  }
/*内外边距*/
  .margin_l_5{
    margin-left: var(--length);
  }
  .margin_r_5{
    margin-right: var(--length);
  }
  .margin_v_5{
    margin-top: var(--length);
    margin-bottom: var(--length);
  }
  .margin_10{
    margin: var(--length2);
  }
  .margin_h_5{
    margin-left: var(--length);
    margin-right: var(--length);
  }
  .margin_b_10,.margin_v_10{
    margin-bottom: var(--length2);
  }
  .margin_v_10{
    margin-top: var(--length2);
  }
  .margin_b_15,.margin_v_15{
    margin-bottom: var(--length3);
  }
  .margin_v_15{
    margin-top: var(--length3);
  }
  .margin_l_20{
    margin-left: 20px;
  }
  .margin_b_20{
    margin-bottom: 20px;
  }
  .margin_b_30{
    margin-bottom: 30px;
  }
  .padding_5{
    padding: var(--length);
  }
  .padding_h_5{
    padding-left: var(--length);
    padding-right: var(--length);
  }
  .padding_v_5{
    padding-top: var(--length);
    padding-bottom: var(--length);
  }
  .padding_10{
    padding: var(--length2);
  }
  .padding_l_10,.padding_h_10{
    padding-left: var(--length2);
  }
  .padding_r_10,.padding_h_10{
    padding-right: var(--length2);
  }
  .padding_v_10{
    padding-top: var(--length2);
    padding-bottom: var(--length2);
  }
  .padding_15{
    padding: var(--length3);
  }
  .padding_l_15{
    padding-left: var(--length3);
  }
  .padding_h_15{
    padding-left: var(--length3);
    padding-right: var(--length3);
  }
  .padding_v_15{
    padding-top: var(--length3);
    padding-bottom: var(--length3);
  }
  .padding_10_15{
      padding: var(--length2) var(--length3);
  }
  .padding_v_20{
    padding: 20px 0;
  }
  .padding_r_20{
    padding-right: 20px;
  }
  .padding_30{
    padding: 30px;
  }
  .padding_b_30{
    padding-bottom: 30px;
  }
  .padding_15_20{
      padding: 19px 15px;
  }
  .padding_r_30{
    padding-right: 30px;
  }
  .padding_r_50{
    padding-right: 70px;
  }
  .padding_v_50{
    padding-top: 50px;
    padding-bottom: 50px;
  }
  .padding_h_70{
    padding-left: 70px;
    padding-right: 70px;
  }
  .padding_b_70{
    padding-bottom: 70px;
  }
/*布局*/
  /*浮动*/
    .pull_left{
      float: left;
    }
    .pull_right{
      float: right;
    }
    .overflow {
    	overflow: hidden;
    }
    .hide{
      display: none;
    }
  /*位置*/
    .absolute._t{
    	top: 0;
    }
    .absolute._l{
    	left: 0;
    }
    .absolute._t._r{
    	right: 0;
    }
    .fixed._t,.fixed._b{
      left: 0;
      width: 100%;
      z-index: 3;
    }
    .fixed._t{
       top:0;
    }
    .fixed._b{
       bottom:0;
    }
    .all_width{
    	width: 100%;
    }
    .all_height{
      height: 100%;
    }
    .all{
      top: 0;
      left: 0;
      height: 100%;
      width: 100%;
    }
    .center{
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
    }
  /*宽度*/
    .main_width{
      width: 1201px;
      margin-left: auto;
      margin-right: auto;
    }
    .width_15{
      width: 15px;
    }
    .width_30{
      width: 30px;
    }
    .width_50{
      width: 50px;
    }
    .width_100{
        width:100px;
    }
    .left_100{
        width: calc(100% - 100px);
    }
    .width_200{
        width:200px;
    }
    .left_200{
        width: calc(100% - 200px);
    }
  /*块状排列*/
    .flex_row_2,.flex_row_3,.flex_row_4{
      display: flex;
      flex-wrap: wrap;
    }
    .flex_row_2 .row_one{
      width: calc(50% - 5px);
    }
    .flex_row_2 .row_one:nth-child(2n-1){
      margin-right: 10px;
    }
    .flex_row_3 .row_one{
      width: calc(33.3% - 10px);
    }
    .flex_row_3 .row_one:nth-child(3n-1),.flex_row_3 .row_one:nth-child(3n-2){
      margin-right: 15px;
    }
    .flex_row_4 .row_one{
      width: calc(25% - 15px);
    }
    .flex_row_4 .row_one:nth-child(4n-1),.flex_row_4 .row_one:nth-child(4n-2),.flex_row_4 .row_one:nth-child(4n-3){
      margin-right: 20px;
    }
  /*宫格*/
    .grid_3,.grid_4{
      overflow: hidden;
      text-align: center;
    }
    .grid_one {
      position: relative;
      padding: 1em 0;
      float: left;
      border-right:1px solid #eee;
      border-bottom:1px solid #eee;
    }
    .grid_3 .grid_one{
      width:33.33%;
    }
    .grid_4 .grid_one{
      width:25%;
    }
    .grid_3 .grid_one:nth-child(3n),.grid_4 .grid_one:nth-child(4n){
      border-right:0;
    }
  /*栅格类*/
    .row {
      overflow: hidden;
    }
    .col_12 {
      width: 100%;
    }
    .col_11 {
      width: 91.66666667%;
    }
    .col_10 {
      width: 83.33333333%;
    }
    .col_9 {
      width: 75%;
    }
    .col_8 {
      width: 66.66666667%;
    }
    .col_7 {
      width: 58.33333333%;
    }
    .col_6 {
      width: 50%;
    }
    .col_5 {
      width: 41.66666667%;
    }
    .col_4 {
      width: 33.33333333%;
    }
    .col_3 {
      width: 25%;
    }
    .col_2 {
      width: 16.66666667%;
    }
    .col_1 {
      width: 8.33333333%;
    }
/*样式*/
  /*遮罩*/
      .mask {
          position: fixed;
          background: rgba(0, 0, 0, 0.3);
          z-index: 8;
          -webkit-transition-duration: 300ms;
      }
      .fade_in {
          opacity: 1;
      }
      .fade_out {
          opacity: 0;
      }
  /*列表*/
    .list .list_item {
      position: relative;
      min-height: 2.2rem;
      padding: 10px 15px;
    }
    .form.list .list_item {
      display: flex;
    }
    .list .list_item:after,.list.list_in .list_item .list_border:after {
      width: 100%;
      height: 1px;
      background: #eee;
      content: '';
      position: absolute;
      bottom: 0;
      left: 0;
    }
    .list.list_in .list_item:after,.list .list_item:last-child:after,.list.list_in .list_item:last-child .list_border:after{
      content:'';
      height: 0;
    }
    .list.list_in .list_item{
      padding: 0 15px;
    }
    .list.list_in .list_item .list_border{
      position: relative;
      padding: 10px 0;
    }
  /*上传图像预览*/
      .imgs{
          overflow: hidden;
          text-align: center;
      }
      .img_one{
          float: left;
          margin: 5px;
      }
      .img_one input{
          opacity: 0;
          z-index: 10
      }
      .img_close{
          position: absolute;
          right: -5px;
          top: -5px;
          width: 20px;
          line-height: 20px;
          background: rgba(0,0,0,.6);
          color: #fff;
          border-radius: 50%;
          z-index: 10;
      }
	/*圆角*/
    .circle{
      border-radius: 50%;
    }
    .circle_3{
      border-radius: 3px;
    }
    .circle_5{
      border-radius: 5px;
    }
    .circle_10{
      border-radius: 10px;
    }
    .circle_15{
      border-radius: 15px;
    }
	/*表单选择对号*/
    .right_block{
      display: block;
      padding:10px 15px;
    }
    .right_input{
      display: none;
    }
    .right_input:checked + .right_icon{
      display: block;
    }
    .right_icon{
      position: absolute;
      top: 10px;
      right: 15px;
      display: none;
    }
  /*单选、多选、开关*/
    input.radio, input.checkbox, input.switch{
      position: relative;
      vertical-align: top;
      border: 1px solid #dddddd;
      -webkit-transition: all 0.2s linear;
    }
    input.radio, input.checkbox {
      width: 1.5em;
      height: 1.5em;
      border-radius: 50%;
    }
    input.checkbox {
       border-radius: 3px;
    }
    input.switch {
      width: 3em;
      height: 1.6em;
      border-radius: 0.8em;
    }
    input.switch:before {
      content: '';
      position: absolute;
      width: 1.43em;
      height: 1.43em;
      top: 0;
      left: 0;
      border-radius: 0.7em;
      background-color: #fff;
      -webkit-transition: left 0.2s;
    }
    input.radio:checked, input.checkbox:checked,input.switch:checked {
      background-color: #03a9f4;
      border: solid 1px #03a9f4;
    }
    input.radio:checked:before,input.radio:disabled:before,input.checkbox:checked:before,input.checkbox:disabled:before{
      content: '';
      position: absolute;
      width: 0.7em;
      height: 0.4em;
      top: calc(50% - 0.35em);
      left: calc(50% - 0.35em);
      border: solid #ffffff;
      border-width: 0 0 1px 1px;
      z-index: 2;
      transform: rotate(-45deg);
    }
    input.switch:checked:before {
       left: 1.43em;
    }
    input.switch,input.radio:disabled,input.radio.disabled,input.checkbox:disabled,input.checkbox.disabled {
      background-color: #dddddd;
      border: solid 1px #dddddd;
    }
  /*滑块*/
    .range {
      position: relative;
      display: inline-block;
      height: 2em;
    }
    .range input[type='range']{
      position: relative;
      height: 0.24em;
      border-radius: 2px;
      background-color: #f0f0f0;
    }
    .range input[type='range']::-webkit-slider-thumb {
      width: 1.6em;
      height: 1.6em;
      border-radius: 50%;
      border-color: #03a9f4;
      background-color: #03a9f4;
      -webkit-appearance: none !important;
    }
    .range .range_tip {
      font-size: 1rem;
      position: absolute;
      z-index: 999;
      top: -1.5rem;
      width: 2.4rem;
      height: 1.5rem;
      line-height: 1.5rem;
      text-align: center;
      color: #666666;
      border: 1px solid #dddddd;
      border-radius: 0.3rem;
      background-color: #ffffff;
      display: none;
    }
    .input-row .range input[type='range'] {
      width: 90%;
      margin-left: 5%;
    }
  /*箭头*/
    .arrow_left,.arrow_right,.arrow_up,.arrow_down{
        position: relative;
    }
    .arrow_left:before,.arrow_right:before,.arrow_up:before,.arrow_down:before{
        position: absolute;
        top: calc(50% - 5px);
        right: 16px;
        width: 6px;
        height: 6px;
        border: 1px solid #dddddd;
        border-top: none;
        border-right: none;
        z-index: 2;
    }
    .arrow_left:before,.arrow_right:before,.arrow_up:before,.arrow_down:before{
        content: '';
    }
    .arrow_left:before {
        -webkit-transform: rotate(45deg);
    }
    .arrow_right:before {
        -webkit-transform: rotate(-135deg);
    }
    .arrow_up:before {
        -webkit-transform: rotate(135deg);
    }
    .arrow_down:before {
        -webkit-transform: rotate(-45deg);
    }
  /*三角形*/
    .triangle_right{
      display: inline-block;
      width: 0;
      height: 0;
      border-top: 6px solid transparent;
      border-bottom: 6px solid transparent;
      border-left: 8px solid #999;
      margin:0 8px;
    }
/*文字*/
	/*字号*/
	  .font_12{
	      font-size: 12px;
	  }
	  .font_14{
	      font-size: 14px;
	  }
	  .font_16{
	      font-size: 16px;
	  }
	  .font_18{
	      font-size: 18px;
	  }
	  .font_20{
	      font-size: 20px;
	  }
	/*对齐方式*/
	  .text_left{
	      text-align: left;
	  }
	  .text_center{
	      text-align: center;
	  }
	  .text_right{
	      text-align: right;
	  }
	/*行间距*/
	  .lineheight_1{
	      line-height: 1;
	  }
	  .lineheight_2{
	      line-height: 2;
	  }
	  .lineheight_3{
	      line-height: 2.4;
	  }
	  .lineheight_4{
	      line-height: 2.8;
	  }
	  .lineheight_5{
	      line-height: 3.2;
	  }
	/*样式*/
	  .font_italic{
	      font-style: italic;
	  }
	  .shadow{
	      box-shadow: 1px 1px 1px rgba(0,0,0,.3);
	  }
    .font_bold{
      font-weight: bold;
    }
	  .text_through{
	      text-decoration: line-through;
	  }
	  .cursor{
	  	cursor: pointer;
	  }
	  .label,button,.btn{
	      display: inline-block;
	      padding: 0 8px;
	      border-radius: 3px;
	      border:1px solid;
	  }
	  button,.btn{
	  	cursor: pointer;
	  }
	  .label{
	      font-size: 12px;
	  }
	  .btn_block {
	      padding: 0.4em 0;
	      text-align: center;
	      border-radius: 5px;
	  }
    .article_title{
      font-size: 26px;
      margin-bottom: 40px;
      font-weight: bold;
      text-align: center;
    }
    .article_text{
      font-size: 16px;
      margin-bottom: 80px;
    }
	/*省略*/
	  .text_ellipsis{
	      white-space: nowrap;
	      text-overflow: ellipsis;
	      overflow: hidden;
	  }
	  .line_ellipsis{
	      display: -webkit-box;
	      -webkit-line-clamp: 4;
	      text-overflow: ellipsis;
	      -webkit-box-orient: vertical;
	      overflow: hidden;
	  }
/*边框*/
  .border{
    border:1px solid #dbdee5;
  }
  .r_border{
    border-right:1px solid;
  }
  .b_border{
    border-bottom: 1px solid;
  }
  .border_none{
    border:none;
  }
  .border_t,.border_b,.border_l,.border_r,.border_tb {
    position: relative;
  }
  .border_l:before,.border_r:after,.border_t:before,.border_b:after{
  	position: absolute;
    background-color: #dbdee5;
    content: '';
    z-index: 2;
  }
  .border_l:before,.border_r:after {
  	top: 0;
    width: 1px;
    height: 100%;
  }
  .border_t:before,.border_b:after {
  	left: 0;
    width: 100%;
    height: 1px;
  }
  .border_l:before {
      left: 0;
  }
  .border_r:after {
      right: 0;
  }
  .border_t:before {
      top: 0;
  }
  .border_b:after {
      bottom: 0;
  }
/*图片*/
  .contain{
    height: 100%;
    object-fit:contain;
  }
  .img_top{
    vertical-align: top;
  }
  .img_middle{
    vertical-align: middle;
  }
  .fit_img{
    height: 100%;
    object-fit: cover;
  }
/*color*/
  .bg_white{
    background: #fff;
  }
  .white{
    color: #fff;
  }
  .bg_light{
    background: #f6f7f9
  }
  .light{
    color: #666;
  }
  .bg_black{
    background: #2E3F4C;
    color: rgba(255, 255, 255, .6)
  }
  .light_white{
    color: rgba(255, 255, 255, .6)
  }
  .black{
    color: #000
  }
  .red{
    color: #ee3131;
  }
  .orange{
    color: #fd7235;
  }
  .blue{
    color:#3c5dc9;
  }
  .bg_blue{
    background:#3c5dc9;
  }
  .btn_blue{
    border-color: #3c5dc9;
    background: #3c5dc9;
    color: #fff;
  }
  .btn_blue:hover{
    background: #1539AF
  }
  .hover:hover a{
    color: #000;
  }



